<template>
	<div class="messagePage">
		<div class="page__hd">
	        <h1 class="page__title"></h1>
	        <p class="page__desc"></p>
	    </div>
		<div class="page__bd">
			 	 <yd-cell-group>
			        <yd-cell-item>
			            <span slot="left"></span>
			            <yd-input slot="right" v-model="input1"  placeholder=""></yd-input>
			        </yd-cell-item>
			        <yd-cell-item>
			            <span slot="left"></span>
			            <yd-input slot="right" v-model="input9" ref="input9" required regex="mobile" placeholder=""></yd-input>
			        </yd-cell-item>
			    </yd-cell-group>
		</div>
	</div>
</template>

<script>
    export default {
        data() {
            return {
                input9: '',
                result: ''
            }
        },
        methods: {
            clickHander() {
                const input = this.$refs.input9;
                this.result = `{<br />  valid${input.valid}<br />  errorMes'${input.errorMsg}'<br />  errorCode'${input.errorCode}'<br />}`;
            }
        }
    }
</script>


